<template>
	
	<div class="subscription-info">
		
		<div v-if="!errorLoading" class="subscription-info__details">
			<template v-if="subscriptionInfo.id">
				<span class="currency-symbol">$</span>
				<span class="amount">{{ subscriptionInfo.amount }}</span>
				<span class="currency">{{ subscriptionInfo.currency }}</span>
				<span class="spacer"></span>
				<span class="frequency">{{ subscriptionInfo.interval }}</span>
			</template>
			<template v-else>
				<vue-simple-spinner
					class="subscription-info__loading-spinner"
					:speed="0.5"
					size="32" />
			</template>
		</div>
		
		<p class="subscription-info__disclaimer">You can cancel your subscription at anytime, and your subscription will be active until the billing month is over.</p>
		
		<go-button
			size="large"
			type="dark"
			right-icon="right-arrow-alt"
			@click="onClickPayment"
			:loading="subscribing"
			:disabled="disablePaymentButton"
			full-width>Go to Payment</go-button>
		
		<stripe-checkout
			ref="checkoutRef"
			mode="subscription"
			:pk="$state.stripeKey"
			:customer-email="$state.user.email"
			:client-reference-id="$state.user.id"
			:line-items="subscriptionPayload.lineItems"
			:success-url="subscriptionPayload.successURL"
			:cancel-url="subscriptionPayload.cancelURL"
			@loading="v => subscribing = v" />
		
	</div>
</template>

<script>
	
	import { StripeCheckout } from '@vue-stripe/vue-stripe';
	import VueSimpleSpinner from 'vue-simple-spinner';
	
	import GoButton from './GoButton.vue';
	
	import {
		STRIPE_SUCCESS_URL,
		STRIPE_CANCEL_URL
	} from '../constants';
	
	export default {
		name: 'GoSubscriptionInfo',
		components: {
			StripeCheckout,
			VueSimpleSpinner,
			GoButton,
		},
		computed: {
			disablePaymentButton() {
				return this.loadingData || this.errorLoading;
			},
		},
		methods: {
			onClickPayment() {
				this.subscribing = true;
				setTimeout(async () => {
					this.$refs.checkoutRef.redirectToCheckout();
				}, 1000);
			},
		},
		async mounted() {
			this.loadingData = true;
			try {
				this.$set(this, 'subscriptionInfo', await this.$subscriptionController.loadSubscriptionInfo());
				this.subscriptionPayload.lineItems[0].price = this.subscriptionInfo.id;
			} catch (error) {
				this.errorLoading = true;
				this.$toast.show({
					title: 'Uh oh!',
					message: error.message,
					duration: 5000,
				});
			}
			this.loadingData = false;
		},
		data() {
			return {
				errorLoading: false,
				loadingData: false,
				subscribing: false,
				subscriptionInfo: {},
				subscriptionPayload: {
					lineItems: [
						{
							price: null,
							quantity: 1,
						},
					],
					successURL: STRIPE_SUCCESS_URL,
					cancelURL: STRIPE_CANCEL_URL,
				},
			};
		}
	}
	
</script>

