			padding: 1rem 0;
		}
		
		&__details {
			
			display: inline-flex;
			column-gap: 0.25rem;
			width: 100%;
			
			padding-bottom: 1rem;
			border-bottom: 2px solid $light-gray;
			
			.currency-symbol {
				font-weight: 800;
			}
			
			.amount {
				font-size: 4rem;
				line-height: 3.7rem;
			}
			
			.currency {
				align-self: flex-end;
			}
			
			.spacer {
				flex: 1;
			}
			
			.frequency {
				align-self: flex-end;
			}
			
		}
		
		&__disclaimer {
			margin-bottom: 2rem;
			font-size: 0.9rem;
