<template>
	<div v-click-outside="hide">
		<div @click="toggle">
			<slot/>
		</div>
		<div v-if="$slots.content" ref="content" class="go-popover" :class="{ 'go-popover--visible': showPopover }" :style="{ 'width': this.width }">
			<div class="go-popover__inner">
				<h6 v-if="$slots.title" class="go-popover__title"><slot name="title" /></h6>
				<div class="go-popover__content" :class="{ 'go-popover__content--padded': padded }">
					<slot name="content">{{ message }}</slot>
				</div>
				<div v-if="$slots.footer" class="go-popover__footer">
					<slot name="footer" />
				</div>
			</div>
			<div data-popper-arrow class="arrow"></div>
		</div>
	</div>
</template>

<script>
	
	import { createPopper } from '@popperjs/core';
	
	import vClickOutside from 'v-click-outside';
	
	export default {
		name: 'GoPopover',
		directives: {
			clickOutside: vClickOutside.directive,
		},
		props: {
			message: String,
			width: {
				type: String,
				default: '400px',
			},
			position: {
				type: String,
				default: 'top',
			},
			visible: {
				type: Boolean,
				default: false,
			},
			padded: {
				type: Boolean,
				default: false,
			}
		},
		methods: {
			toggle() {
				this.showPopover = !this.showPopover;
			},
			show() {
				this.showPopover = true;
			},
			hide() {
				this.showPopover = false;
			}
		},
		watch: {
			async showPopover(visible) {
				this.$emit(visible ? 'show' : 'hide');
				await this.popper.update();
			},
		},
		mounted() {
			let anchor = this.$slots.default[0].elm;
			let popover = this.$refs.content;
			this.popper = createPopper(anchor, popover, {
				placement: this.position,
				strategy: 'fixed',
				modifiers: [
					{
						name: 'arrow',
						options: {
							padding: 32,
						}
					},
					{
						name: 'offset',
						options: {
							offset: [0, 20],
						},
					},
					{
						name: 'preventOverflow',
						options: {
							padding: 20,
						},
					},
				],
			});
		},
		data() {
			return {
				popper: null,
				showPopover: false,
			}
		}
	}
	
</script>

<style lang="scss" scoped>
	
	@import '@/assets/css/all.scss';

	$arrow-size: 2rem;
	$arrow-size-half: ($arrow-size * 0.5);
	
	.arrow,
	.arrow::before {
		position: absolute;
		width: $arrow-size;
		height: $arrow-size;
		border-radius: ($arrow-size / 4);
		background: inherit;
	}
	
	.arrow {
		visibility: hidden;
	}

	.arrow::before {
		visibility: visible;
		content: '';
		transform: rotate(45deg);
		left: 0;
	}
	
	.go-popover {
		
		opacity: 0;
		pointer-events: none;
		z-index: 100000;
		text-align: center;
		padding: 1.5rem;
		background-color: white;
		border-radius: 1.5rem;
		box-sizing: border-box;
		box-shadow:
			0 0.7px 2.2px rgba(0, 0, 0, 0.014),
			0 1.6px 5.3px rgba(0, 0, 0, 0.02),
			0 3px 10px rgba(0, 0, 0, 0.025),
			0 5.4px 17.9px rgba(0, 0, 0, 0.03),
			0 10px 33.4px rgba(0, 0, 0, 0.036),
			0 24px 80px rgba(0, 0, 0, 0.05)
		;
		
		&__title {
			color: $dusty-blue;
			margin: 0;
		}
		
		&__content {
			font-size: 0.8rem;
			p {
				margin: 0 0 1rem 0;
			}
			p:last-of-type {
				margin-bottom: 0;
			}
			&--padded {
				padding: 1.5rem 4rem;
			}
		}
		
		&--visible {
			opacity: 1;
			pointer-events: painted;
		}
		
	}
	
	.go-popover[data-popper-placement^='top'] > .arrow {
		bottom: -($arrow-size-half - 0.25rem);
	}
	
	.go-popover[data-popper-placement^='bottom'] > .arrow {
		top: -($arrow-size-half - 0.25rem);
	}
	
	.go-popover[data-popper-placement^='left'] > .arrow {
		right: -$arrow-size-half;
	}
	
	.go-popover[data-popper-placement^='right'] > .arrow {
		left: -$arrow-size-half;
	}
	
</style>