import firebase from 'firebase/app';
import 'firebase/auth';

const Error = {
	EMAIL_EXISTS_WITH_DIFFERENT_CREDENTIALS: 'auth/account-exists-with-different-credential'
};

const AuthProvider = {
	GOOGLE: firebase.auth.GoogleAuthProvider,
	FACEBOOK: firebase.auth.FacebookAuthProvider,
	TWITTER: firebase.auth.TwitterAuthProvider,
};

const DomainToAuthProvider = {
	['google.com']: 'GOOGLE',
	['facebook.com']: 'FACEBOOK',
	['twitter.com']: 'TWITTER',
};

class AuthController {
	
	async signOut() {
		return await firebase.auth().signOut();
	}
	
	async signInWithProvider(AuthProviderClass) {
		const auth = firebase.auth();
		return await auth.signInWithPopup(new AuthProviderClass())
			.catch(async (e) => {
				if (e.code === Error.EMAIL_EXISTS_WITH_DIFFERENT_CREDENTIALS) {
					const newAuthProviderCredential = e.credential;
					const authMethods = await auth.fetchSignInMethodsForEmail(e.email);
					throw {
						type: 'link-account',
						authResponse: {
							newAuthProviderCredential,
							newAuthProvider: DomainToAuthProvider[newAuthProviderCredential.providerId],
							originalAuthProvider: DomainToAuthProvider[authMethods[0]],
						}
					};
				}
				throw e;
			});
		//console.log(result);
		//const linked = await firebase.auth().currentUser.linkWithPopup(provider);
		//console.log(linked);
		//return result;
	}
	
	async authenticate(providerKey = '') {
		const AuthProviderClass = AuthProvider?.[providerKey.toUpperCase()];
		if (AuthProviderClass) {
			return await this.signInWithProvider(AuthProviderClass);
		}
	}
	
	async linkAccount(providerCredential) {
		const currentUser = firebase.auth().currentUser;
		const linkCredentialResponse = await currentUser.linkWithCredential(providerCredential);
		return await firebase.auth().signInWithCredential(linkCredentialResponse.credential);
	}
	
}

export default new AuthController();