<template>
	<go-button class="social-media-button" :class="{ 'social-media-button--disabled': disabled }" size="medium" type="ghost" full-width @click="onClick">
		<div class="social-media-button__inner">
			<img v-if="icon" class="icon" :src="`/img/icons/${icon}.svg`" :alt="label">
			<span v-if="label">{{ label }}</span>
		</div>
	</go-button>
</template>

<script>
	
	import GoButton from './GoButton.vue'
	
	export default {
		name: 'GoSignupButton',
		components: { GoButton },
		props: {
			icon: {
				type: String,
				default: '',
			},
			label: {
				type: String,
				default: '',
			},
			disabled: {
				type: Boolean,
				default: false,
			}
		},
		methods: {
			onClick() {
				if (this.disabled) return;
				this.$emit('click'); 
			}
		}
	}
	
</script>

<style lang="scss" scoped>
	
	.social-media-button {
		
		padding: 1rem !important;
		font-weight: 400;
		
		&__inner {
			display: grid;
			grid-template-columns: 3rem auto 3rem;
			align-items: center;
			text-align: center;
			width: 100%;
		}
		
		.icon {
			width: 2rem;
			height: 2rem;
		}
		
		&--disabled {
			opacity: 0.25;
			pointer-events: none;
		}
		
	}
	
</style>