import Vue from 'vue';

export const watch = (obj, expOrFn, cb, opts) => {
  const vm = new Vue({
    data: obj
  });
  return vm.$watch(expOrFn, cb, opts);
}

export const prettyTime = (duration) => {
  const hours = Math.floor(duration)
  const minutes = (duration * 60) % 60;
  const parts = [];
  if (hours) {
    if (hours > 1) {
      parts.push(`${hours}HRs`);
    } else {
      parts.push(`${hours}HR`);
    }
  }
  if (minutes) {
    parts.push(`${minutes} Mins`);
  }
  return parts.join(' & ');
};