<template>
	
	<div class="view login-view">
		
		<div class="login-view__left light-rays-1">
			<img class="login-view__illustration" src="/img/login-screen-illustration.png" alt="">
		</div>
		
		<div class="login-view__right">
			
			<h2 class="login-view__header">Login or Signup</h2>
			
			<div class="login-view__buttons">
				<go-signup-button icon="google" label="Continue with Google" @click="onClickLoginButton('Google')" />
				<go-signup-button icon="facebook" label="Continue with Facebook" @click="onClickLoginButton('Facebook')" />
				<go-signup-button icon="twitter" label="Continue with Twitter" @click="onClickLoginButton('Twitter')" />
			</div>
			
			<p class="login-view__disclaimer">By signing up you agree to our <go-button size="small" type="link" @click="onClickShowLegals">Privacy Policy and Terms of Service</go-button>.</p>
			
			<vue-simple-spinner
				v-if="$state.authenticating"
				class="login-view__loading-spinner"
				:speed="0.5"
				size="64" />
			
		</div>
		
		<go-modal
			v-show="linkAccountModal.visible"
			width="24rem"
			title="Link Account"
			centre-text
			@click-background="onClickCloseLinkAccountModal">
			<p>You have already used the email address associated with your {{ linkAccountModal.newProvider }} account to create a {{ $constants.APP_NAME }} account.</p>
			<p>Would you like to link your {{ linkAccountModal.newProvider }} details to that account?</p>
			<div style="display: flex; flex-direction: column; align-items: center;">
				<go-button type="link" @click="onClickLinkAccount">LINK ACCOUNT</go-button>
				<go-button type="link	" size="small" @click="onClickCloseLinkAccountModal">CLOSE</go-button>
			</div>
		</go-modal>
		
		<go-legals-modal
			v-show="legalsModal.visible"
			@click-background="legalsModal.visible = false"
		/>
		
		<div class="fixed-top-left">
			<img class="logo" src="/img/logo.svg" alt="">
		</div>
		
	</div>
	
</template>

<script>
	
	import firebase from 'firebase/app';
	import 'firebase/functions';
	
	import VueSimpleSpinner from 'vue-simple-spinner';
	
	import GoButton from '../components/GoButton';
	import GoModal from '../components/GoModal';
	import GoSignupButton from '../components/GoSignupButton.vue';
	
	import GoLegalsModal from '../components/Modals/Legals.vue';
	
	export default {
		name: 'Login',
		components: {
			VueSimpleSpinner,
			GoButton,
			GoModal,
			GoSignupButton,
			GoLegalsModal
		},
		methods: {
			showErrorMessage(error = {}) {
				let title = 'Uh oh!';
				let message = error.message;
				if (error.code == 'auth/popup-closed-by-user') {
					title = 'Login Cancelled';
					message = 'Come back anytime!';
				}
				this.$toast.show({
					title,
					message,
					duration: 5000,
				});
			},
			loadHomeScreen() {
				// this.$state.authenticating = false;
				this.$router.replace({ name: 'home' });
			},
			async saveReferral() {
				if (this.$state.referrer) {
					await firebase.functions().httpsCallable('saveReferral')({
						referrer: this.$state.referrer,
					});
					this.$state.referrer = null;
				}
			},
			async onClickLoginButton(authProvider) {
				this.$state.authenticating = true;
				this.linkAccountModal.newProvider = authProvider;
				try {
					await this.$authController.authenticate(authProvider);
					await this.saveReferral();
				} catch (error) {
					if (error.type === 'link-account') {
						this.linkAccountModal.visible = true;
						this.linkAccountModal.authResponse = error.authResponse;
					} else {
						this.showErrorMessage(error);
					}
				} finally {
					this.$state.authenticating = false;
				}
			},
			onClickCloseLinkAccountModal() {
				this.linkAccountModal.visible = false;
			},
			async onClickLinkAccount() {
				this.$state.authenticating = true;
				try {
					await this.$authController.authenticate(this.linkAccountModal.authResponse.originalAuthProvider);
					await this.$authController.linkAccount(this.linkAccountModal.authResponse.newAuthProviderCredential);
					await this.saveReferral();
				} catch (error) {
					this.showErrorMessage(error);
				} finally {
					this.$state.authenticating = false;
				}
			},
			onClickShowLegals() {
				this.legalsModal.visible = true;
			}
		},
		watch: {
			'$state.user': {
				deep: true,
				handler(user) {
					if (user.id) {
						setTimeout(() => {
							this.loadHomeScreen();
						}, 500);
					}
				}
			}
		},
		async mounted() {
			
			const urlParams = new URLSearchParams(window.location.search);
			
			this.$state.referrer = urlParams.get('referrer');
			
			this.$musicController.pause();
			
		},
		data() {
			return {
				linkAccountModal: {
					visible: false,
					newProvider: '',
					authResponse: {},
				},
				legalsModal: {
					visible: false,
				}
			}
		}
	}
	
</script>

