<template>
	<div id="timeline" class="go-timeline">
		<div id="timeline-inner" class="go-timeline__inner" :style="timelineStyle">
		<!-- Markers -->
		<div class="go-timeline__markers">
			<span v-for="(marker, index) in markers" class="go-timeline-marker" :key="index">{{ marker }}</span>
		</div>
		<!-- Progress -->
		<div class="go-timeline__progress">
			<div class="go-timeline__progress-inner" :style="progressStyle"></div>
			<div class="go-timeline__particle-emitter-source" :style="particleEmitterSourceStyle"></div>
		</div>
		<!-- Cards -->
		<div class="go-timeline-cards">
			<draggable
				v-if="showTasks"
				v-model="tasks"
				class="go-timeline-cards__inner"
				handle=".drag-handle"
				drag-class="dragging"
				ghost-class="ghosting"
				:force-fallback="!isFirefox"
				:fallback-on-body="!isFirefox"
				@change="onChangeOrder">
				<transition-group id="timeline-cards" class="go-timeline-cards__cards">
					<go-timeline-card
						v-for="(task, index) in tasks"
						class="min-card-height resizable"
						:data-index="index"
						:data="task"
						:day-length="dayLength"
						:key="index"
						@hover="onHoverTask"
						@travel="onHoverTask"
						@leave="onLeaveTask"
						@input.native="onLeaveTask"
						@delete-task="onDeleteTask(index)"
						@complete-task="onCompleteTask(index)"/>
				</transition-group>
			</draggable>
			<div v-if="!showTasks" class="add-first-task-button-wrapper">
				<go-button
					class="min-card-height add-first-task-button"
					type="ghost"
					@click="onClickAddTask">
					{{ $constants.START_DAY_BUTTON_LABEL }}
				</go-button>
			</div>
			<go-button
				v-if="showTasks"
				v-show="$state.canAddTask"
				class="add-task-button"
				type="link"
				size="large"
				right-icon="plus"
				v-attach-element="{ container: 'timeline-cards' }"
				@click="onClickAddTask" />
		</div>
	</div>
	</div>
</template>

<script>
	
	import interact from 'interactjs';
	import draggable from 'vuedraggable';
	
	import GoTimelineCard from './GoTimelineCard.vue';
	import GoButton from './GoButton';
	
	import party from 'party-js';
	
	import { prettyTime } from '../utils';
	
	export default {
		name: 'GoTimeline',
		components: {
			draggable,
			GoTimelineCard,
			GoButton
		},
		props: {
			value: {
				type: Array,
				default: () => ([]),
			},
			dayLength: {
				type: Number,
				default: 60
			},
			progress: {
				type: Number,
				default: 0,
			}
		},
		computed: {
			zoomLevel() {
				return this.$state.timelineZoom;
			},
			timelineStyle() {
				return {
					width: `${this.zoomLevel}%`,
				}
			},
			showTasks() {
				return this.tasks.length;
			},
			markers() {
				const markers = ['Start'];
				if (this.zoomLevel >= 100 && this.zoomLevel < 150) {
					markers.push(prettyTime(this.dayLength * 0.50));
				} else if (this.zoomLevel >= 150 && this.zoomLevel < 200) {
					markers.push(prettyTime(this.dayLength * 0.25));
					markers.push(prettyTime(this.dayLength * 0.50));
					markers.push(prettyTime(this.dayLength * 0.75));
				} else if (this.zoomLevel >= 200) {
					markers.push(prettyTime(this.dayLength * 0.125));
					markers.push(prettyTime(this.dayLength * 0.250));
					markers.push(prettyTime(this.dayLength * 0.375));
					markers.push(prettyTime(this.dayLength * 0.500));
					markers.push(prettyTime(this.dayLength * 0.625));
					markers.push(prettyTime(this.dayLength * 0.750));
					markers.push(prettyTime(this.dayLength * 0.875));
				}
				markers.push(prettyTime(this.dayLength));
				return markers;
			},
			progressStyle() {
				return {
					width: `${this.progress}%`,
				}
			},
			particleEmitterSourceStyle() {
				return {
					left: `${this.progress}%`,
				}
			}
		},
		methods: {
			async getTimelineInnerWidth() {
				await this.$nextTick();
				return document.getElementById('timeline-inner').getBoundingClientRect().width;
			},
			async emitParticles() {
				await this.$nextTick();
				const particleEmitterSource = document.querySelector(".go-timeline__particle-emitter-source");
				this.particleEmitter = party.scene.current.createEmitter({
					emitterOptions: {
						duration: 1000000,
						useGravity: false,
						modules: [
							new party.ModuleBuilder()
								.drive('size')
								.by((t) => 0.6 + 0.3 * (Math.cos(t * 10) + 1))
								.build(),
							new party.ModuleBuilder()
								.drive('opacity')
								.by((t) => t)
								.through('size')
								.build(),
						],
					},
					emissionOptions: {
						rate: 4,
						sourceSampler: party.sources.dynamicSource(particleEmitterSource),
						angle: party.variation.skew(180,party.variation.evaluateVariation(party.variation.range(15, 30))),
						initialLifetime: 1,
						initialSpeed: 40,
						initialColor: party.Color.fromHex("#9DE1A3")
					},
					rendererOptions: {
						shapeFactory: ["circle"],
						applyLighting: () => true,
					},
				});
			},
			stopParticles() {
				if (this.particleEmitter) {
					this.particleEmitter.options.duration = 0;
					this.particleEmitter.clearParticles();
					this.particleEmitter = null;
				}
			},
			onChangeOrder() {
				this.$emit('input', this.tasks);
				this.$taskController.resolveTaskStatus();
			},
			onHoverTask(e) {
				this.activeCard = e.element;
				const rect = this.activeCard.getBoundingClientRect();
				if (rect.width <= 120) {
					this.activeCard.classList.add('show-info');
				}
			},
			onLeaveTask(e) {
				this.activeCard.classList.remove('show-info');
			},
			onDeleteTask(index) {
				this.$taskController.deleteTask(index);
				this.$taskController.resolveTaskStatus();
			},
			onCompleteTask(index) {
				this.$taskController.toggleTaskStatus(index);
			},
			onClickAddTask() {
				this.$taskController.addNewTask();
				this.$taskController.resolveTaskStatus();
			}
		},
		mounted() {
			
			let distanceTravelled = 0;
			
			let totalMinutes = 0;
			let totalPixels = 0;
			let pixelsToMinutesFactor = 0;
			
			let durationStepSize = 5;
			let pixelThreshold = 0;
			
			this.interaction = interact('.resizable').resizable({
				edges: { right: true },
				listeners: {
					start: (e) => {
						e.target.classList.add('resizing');
						// resizeEvents = [];
						distanceTravelled = 0;
						this.disableDrag = true;
						totalMinutes = this.$state.dayLengthInMinutes;
						totalPixels = e.target.parentNode.getBoundingClientRect().width;
						pixelsToMinutesFactor = totalPixels / totalMinutes;
						// pixelsToMinutes = pixelsToMinutesFactor * totalMinutes;
						pixelThreshold = pixelsToMinutesFactor * durationStepSize;
						// console.log(totalPixels, pixelsToMinutesFactor, pixelThreshold);
					},
					move: (e) => {
						const target = e.target;
						const index = target.getAttribute('data-index');
						const task = this.tasks[index];
						const adding = (e?.delta?.x > 0);
						const subtracting = !adding;
						if (adding && this.$state.dayIsFull) {
							return;
						}
						if (subtracting && task.duration <= this.$constants.MIN_TASK_LENGTH) {
							return;
						}
						if (e?.delta?.x) {
							distanceTravelled += e.delta.x;
							// console.log(distanceTravelled, pixelThreshold, (distanceTravelled >= pixelThreshold), (distanceTravelled >= pixelThreshold));
							if (Math.abs(distanceTravelled) >= pixelThreshold) {
								distanceTravelled = 0;
								if (subtracting) {
									task.duration -= durationStepSize;
								} else {
									task.duration += durationStepSize;
								}
							}
						}
						// resizeEvents.push(e);
					},
					end: (e) => {
						e.target.classList.remove('resizing');
						// console.log(resizeEvents[0]);
						// console.log(resizeEvents.pop());
						this.disableDrag = false;
						this.$taskController.resolveTaskStatus();
					}
				},
			});
		},
		beforeDestroy() {
			this.stopParticles();
		},
		watch: {
			'$state.timelineZoomHandlePositions': {
				deep: true,
				async handler(values) {
					const [ minX ] = values;
					const leftPosFraction = (minX / this.$constants.MAX_TIMELINE_ZOOM_PERCENT);
					const timeline = document.getElementById('timeline');
					const timelineWidth = await this.getTimelineInnerWidth();
					timeline.scrollTo({ left: (timelineWidth * leftPosFraction) });
				}
			},
			'$state.dayHasStarted': {
				immediate: true,
				handler(dayHasStarted) {
					if (dayHasStarted && !this.$state.dayHasFinished) {
						this.emitParticles();
					}
				},
			},
			'$state.dayProgress': {
				immediate: true,
				handler(dayProgress) {
					if ([0, 100].includes(dayProgress)) {
						this.stopParticles();
					}
				},
			},
			value(newValue) {
				this.$set(this, 'tasks', newValue);
			}
		},
		data() {
			return {
				interaction: null,
				drag: false,
				disableDrag: false,
				disableResize: false,
				tasks: this.value,
				activeCard: null,
				particleEmitter: null,
				isFirefox: navigator.userAgent.toLowerCase().indexOf('firefox') > -1,
			}
		}
	}
	
</script>

<style lang="scss" scoped>
	
	@import '@/assets/css/all.scss';
	
	.min-card-height {
		min-height: 104px;
	}
	
	.add-first-task-button-wrapper {
		display: flex;
		align-items: center;
	}
	
	.add-first-task-button {
		margin: 0 auto;
		padding: 0 5rem !important;
		font-size: 0.8rem !important;
		font-weight: 600;
	}
	
	.add-task-button {
		position: absolute;
		padding: 2rem !important;
		margin-left: 1rem;
	}
	
	.go-timeline {
		
		display: grid;
		grid-template-rows: auto auto 1fr;
		user-select: none;
		// padding: 0 2rem;
		overflow: hidden;
		
		&::-webkit-scrollbar {
			height: 0;
			display: none;
		}
		
		&__inner {
			padding: 0 2rem;
			box-sizing: border-box;
		}
		
		&__markers {
			display: flex;
			justify-content: space-between;
			margin-bottom: 0.5rem;
		}
		
		&-marker {
			font-size: 0.8rem;
			font-weight: 800;
			color: $dusty-blue;
			text-transform: uppercase;
		}
		
		&__progress {
			background-color: $light-gray;
			height: 0.5rem;
			border-radius: 0.25rem;
			overflow: hidden;
			margin-bottom: 1.5rem;
			position: relative;
			&-inner {
				transition: all 100ms;
				background-color: $green;
				border-radius: 0.25rem;
				height: 100%;
			}
		}
		
		&__particle-emitter-source {
			display: block;
			width: 0.5rem;
			height: 0.5rem;
			position: absolute;
			top: 0;
			left: 0;
			transform: translateX(-100%) translateY(-100%);
		}
		
		.go-timeline-cards {
			// background-color: #f5f5f5;
			// border-top: 2px solid #f2f2f2;
			// border-bottom: 2px solid #f2f2f2;
			// padding: 0.5rem 0;
			&__inner {
				display: flex;
				box-sizing: border-box;
			}
			&__cards {
				width: 100%;
				height: 100%;
				display: inline-flex;
				align-items: stretch;
			}
		}
		
	}
	
</style>