<template>
	<div class="go-modal-background" @click.self="$emit('click-background')">
		<div class="go-modal" :style="modalStyle" :class="{
			'go-modal--no-padding': noPadding,
			'go-modal--full-screen': fullScreen,
			'go-modal--centre-text': centreText
			}">
			<h3 v-if="title || $slots.title" class="go-modal__title"><slot name="title">{{ title }}</slot></h3>
			<slot />
		</div>
	</div>
</template>

<script>
	export default {
		name: 'GoModal',
		props: {
			title: String,
			backgroundImage: String,
			noPadding: Boolean,
			fullScreen: Boolean,
			centreText: Boolean,
			width: {
				type: String,
				default: '100%',
			},
			maxWidth: {
				type: String,
				defaut: '960px',
			},
			padding: {
				type: String,
				default: '',
			},
		},
		computed: {
			modalStyle() {
				return {
					backgroundImage: `url("${this.backgroundImage}")`,
					width: this.width,
					maxWidth: this.maxWidth,
					padding: this.padding,
				}
			}
		}
	}
</script>

