<template>
	<go-grid v-if="!$state.userSubscriptionCancelled">
		<go-grid-column>
			<h6>SUBSCRIPTION</h6>
			<span>${{ $state.userSubscription.amount }} Dollars Per Month</span>
		</go-grid-column>
		<go-grid-column alignment="right">
			<go-popover ref="popover" position="top-end" padded>
				<go-button type="link" size="small">CANCEL SUBSCRIPTION</go-button>
				<template #title>Cancel Subscription</template>
				<template #content>
					<p>You currently have <strong>{{ $state.userSubscription.daysRemaining }} days</strong> left of your billing month.</p>
					<p>If you cancel now you can continue using {{ $constants.APP_NAME }} until the billing month is over.</p>
				</template>
				<template #footer>
					<go-button size="small" :type="cancelButtonType" :loading="cancelling" @click="onClickCancelSubscription">YES, CANCEL SUBSCRIPTION</go-button>
				</template>
			</go-popover>
		</go-grid-column>
	</go-grid>
	<go-grid v-else>
		<go-grid-column width="2fr">
			<span v-html="inactiveSubscriptionLabel"></span>
		</go-grid-column>
		<go-grid-column width="1fr" alignment="right">
			<go-button type="link" size="small" @click="$emit('subscribe')">{{ inactiveSubscriptionButtonLabel }}</go-button>
		</go-grid-column>
	</go-grid>
</template>

<script>
	
	import GoGrid from './GoGrid.vue';
	import GoGridColumn from './GoGridColumn.vue';
	import GoButton from './GoButton.vue';
	import GoPopover from './GoPopover.vue';
	
	export default {
		name: 'GoSubscription',
		components: {
			GoGrid,
			GoGridColumn,
			GoButton,
			GoPopover
		},
		computed: {
			cancelButtonType() {
				if (this.cancelling) return 'link';
				return 'light';
			},
			inactiveSubscriptionLabel() {
				if (this.$state.userHasSubscription && this.$state.userSubscriptionCancelled) return `You have have <strong>${this.$state.userSubscription.daysRemaining} days</strong> left before your subscription expires.`;
				return 'You have no active subscription.';
			},
			inactiveSubscriptionButtonLabel() {
				if (this.$state.userHasSubscription && this.$state.userSubscriptionCancelled) return 'RENEW SUBSCRIPTION';
				return 'SUBSCRIBE NOW';
			}
		},
		methods: {
			onClickCancelSubscription() {
				if (this.cancelling) return;
				this.cancelling = true;
				this.$emit('cancel', () => {
					if (this.$refs.popover) {
						this.$refs.popover.hide();
					}
					this.cancelling = false;
				});
			}
		},
		data() {
			return {
				cancelling: false,
			}
		}
	}
	
</script>

