<template>
	<div class="go-button"
		:class="buttonClasses"
		@click="onClick">
		<div class="go-button__inner">
			<template v-if="loading">
				<vue-simple-spinner
					:speed="0.5"
					size="16" />
			</template>
			<template v-else>
				<slot name="leftIcon">
					<i v-if="leftIcon" :class="leftIconClass"></i>
				</slot>
				<slot />
				<slot name="rightIcon">
					<i v-if="rightIcon" :class="rightIconClass"></i>
				</slot>
			</template>
		</div>
	</div>
</template>

<script>
	
	import VueSimpleSpinner from 'vue-simple-spinner';
	
	export default {
		name: 'GoButton',
		props: {
			leftIcon: [ String, Object ],
			rightIcon: [ String, Object ],
			loading: Boolean,
			type: {
				type: String,
				default: 'default',
			},
			size: {
				type: String,
				default: 'medium',
			},
			iconSize: {
				type: String,
				default: 'medium',
			},
			border: {
				type: Boolean,
				default: false,
			},
			noBorder: {
				type: Boolean,
				default: false,
			},
			fullWidth: {
				type: Boolean,
				default: false,
			},
			spacedElements: {
				type: Boolean,
				default: false,
			},
			disabled: {
				type: Boolean,
				default: false,
			}
		},
		components: {
			VueSimpleSpinner
		},
		computed: {
			buttonClasses() {
				return [
					this.type,
					this.size,
					{
						'spaced-elements': this.spacedElements,
						'full-width': this.fullWidth,
						'border': this.border,
						'no-border': this.noBorder,
						'disabled': this.disabled,
					}
				];
			},
			leftIconClass() {
				let prefix = '';
				let name = this.leftIcon;
				if (this.leftIcon?.name) {
					prefix = this.iconPrefixes[this.leftIcon?.type ?? 'regular'];
					name = this.leftIcon?.name ?? '';
				}
				return [
					`bx bx${prefix}-${name}`,
					this.iconSize,
				];
			},
			rightIconClass() {
				if (this.rightIcon?.name) {
					const prefix = this.iconPrefixes[this.rightIcon?.type ?? 'regular'];
					const name = this.rightIcon?.name ?? '';
					return `bx bx${prefix}-${name}`;
				}
				return `bx bx-${this.rightIcon}`;
			},
		},
		methods: {
			onClick() {
				if (this.disabled) return;
				this.$emit('click');
			}
		},
		data() {
			return {
				iconPrefixes: {
					regular: '',
					solid: 's',
					logo: 'l',
				}
			}
		}
	}
	
</script>

