		transition: all 100ms;
		box-sizing: border-box;
		font-weight: 700;
		
		&__inner {
			display: flex;
			align-items: center;
			column-gap: 0.5rem;
		}
		
		&:hover {
			opacity: 0.75;
		}
		
		/* Icon */
		
		i {
			&.large {
				font-size: 1.5rem;
			}
