				font-size: 2rem;
			}
		}
		
		/* Styles */
		
		&.default {
			background-color: $blue;
			color: #ffffff;
		}
		
		&.light {
			background-color: white;
			border: 1px solid #E1E7F0;
			color: $blue;
		}
		
		&.dark {
			background-color: $charcoal;
			color: white;
		}
		
		&.ghost {
			background-color: transparent;
