<template>
  <div class="go-referrals">
    <div class="referral-information">
      <div class="referral-information__headings">
        <p>Refer a Friend,</p>
        <p>Earn Money</p>
      </div>
      <div class="referral-information__content">
        <p class="referral-information__instructions">If you refer a friend, we will pay you $1 for each month they keep their subscription active.</p>
        <p class="referral-information__instructions-eg">E.G: 20 SUBSCRIBED FRIENDS = <span class="usd">20 USD PER MONTH</span></p>
        <div class="referral-link" @click="onClickCopy">
          <p class="referral-link__url" @mouseover="onHoverReferralLink" @mouseleave="onLeaveReferralLink">{{ referralURL }}</p>
          <go-button class="referral-link__copy-button" type="link" size="large">
            <template #leftIcon>
              <img style="width: 2rem; height: 2rem;" src="/img/icons/boxes.svg">
            </template>
          </go-button>
        </div>
      </div>
    </div>
    <div v-if="showPaymentInformation" class="payment-information">
      <go-button
        type="light"
        size="small"
        icon-size="large"
        :left-icon="getPaidButtonIcon"
        @click="$emit('click-get-paid')"
        no-border
      >
      
        {{ getPaidButtonLabel }}
      </go-button>
      <p class="payment-information__active-referrals">
        <span class="title">ACTIVE REFERRALS:</span>
        <span class="count">{{ referralCount }} FRIENDS</span>
        <span> = </span>
        <span class="usd">{{ referralCount }} USD PER MONTH</span>
        <span class="fire"></span>
      </p>
    </div>
  </div>
</template>

<script>
  
  import GoButton from './GoButton.vue';
  import GoCircleButton from './GoCircleButton.vue';
  import GoGrid from './GoGrid.vue';
  import GoGridColumn from './GoGridColumn.vue';
  import GoGridRow from './GoGridRow.vue';
  
  export default {
    name: 'GoReferrals',
    components: {
      GoButton,
      GoCircleButton,
      GoGrid,
      GoGridColumn,
      GoGridRow,
    },
    computed: {
      referralURL() {
        return `${window.location.origin}?referrer=${this.$state.user.id}`;
      },
      referralCount() {
        return this.$state.userReferrals.length ?? 0;
      },
      showPaymentInformation() {
        return this.referralCount > 0;
      },
      payPalEmailAddress() {
        return this.$state.userSettings.paypalEmailAddress;
      },
      hasPayPalEmailAddress() {
        return this.payPalEmailAddress ? true : false;
      },
      getPaidButtonIcon() {
        return this.hasPayPalEmailAddress ? { name: 'paypal', type: 'logo' } : null;
      },
      getPaidButtonLabel() {
        return this.hasPayPalEmailAddress ? this.payPalEmailAddress : 'GET PAID';
      }
    },
    methods: {
      onHoverReferralLink(e) {
        this.referralLinkTooltip = this.$tooltip.show(e.target, {
          title: 'Your Referral Link',
          message: this.referralURL,
          width: '240px',
        });
      },
      onLeaveReferralLink(e) {
        if (this.referralLinkTooltip) {
          this.referralLinkTooltip.hide();
        }
      },
      onClickCopy() {
        this.$copyText(this.referralURL).then((e) => {
          this.$toast.show({
            title: 'Success!',
            message: 'Your referral link has been copied to the clipboard... send it to your friends!',
            duration: 5000,
          });
        });
      }
    },
    data() {
      return {
        referralLinkTooltip: null,
      }
    }
  }
  
</script>

