<template>
	<div class="go-grid" :style="gridStyle">
		<slot />
	</div>
</template>

<script>
	export default {
		name: 'GoGrid',
		props: {
			rows: {
				type: Boolean,
				default: false,
			},
			gutter: {
				type: String,
				default: '1rem',
			},
			noGutter: {
				type: Boolean,
				default: false,
			}
		},
		computed: {
			isColumnGrid() {
				return this.rows === false;
			},
			isRowGrid() {
				return this.rows === true;
			},
			gridStyle() {
				if (this.isColumnGrid) {
					const columnWidths = this.$slots.default.map(node => node?.componentOptions?.propsData?.width ?? '1fr');
					return {
						gridTemplateColumns: columnWidths.join(' '),
						columnGap: this.noGutter ? null : this.gutter,
					}
				}
				const rowHeights = this.$slots.default.map(node => node?.componentOptions?.propsData?.height ?? '1fr');
				return {
					gridTemplateRows: rowHeights.join(' '),
					rowGap: this.noGutter ? null : this.gutter,
				}
			}
		},
	}
</script>

<style lang="scss" scoped>
	
	.go-grid {
		display: grid;
		height: 100%;
	}
	
</style>