import {
	DEFAULT_DAY_LENGTH,
	MIN_TASK_LENGTH,
	TRIAL_DAYS,
} from './constants'; 

import Crate from './plugins/crate';

const state = new Crate({
	
	authenticating: false,
	
	referrer: null,
	
	dayLength: DEFAULT_DAY_LENGTH,
	dayProgressInSeconds: 0,
	tasks: [],
	
	user: {},
	userSubscription: {},
	userSubscriptionLoaded: false,
	userReferrals: [],
	userSettings: {},
	
	subscriptionInfo: {},
	
	dayHasStarted: false,
	
	timelineZoom: 100,
	timelineZoomHandlePositions: [0, 0],
	timelineActiveZoomHandle: 0,
	
	stripeKey() {
		return process.env.VUE_APP_STRIPE_PUBLISHABLE_KEY;
	},
	
	trialAccountExpired() {
		const start = new Date(this.user.registrationDate);
		const today = new Date();
		const diffInTime = (today.getTime() - start.getTime());
		const diffInDays = (diffInTime / (1000 * 3600 * 24));
		return (diffInDays >= TRIAL_DAYS) && !this.userHasSubscription;
	},
	
	userRegistrationDate() {
		return new Date(this.user.registrationDate);
	},
	
	userSubscriptionExpired() {
		return this.userSubscription?.expired ?? true;
	},
	
	userSubscriptionCancelled() {
		return this.userSubscription?.cancelled ?? true;
	},
	
	userHasSubscription() {
		if (Object.keys(this.userSubscription).length) {
			return !this.userSubscription.expired;
		}
		return false;
	},
	
	userIsLoggedIn() {
		return this.user?.id ? true : false;
	},
	
	dayHasFinished() {
		return (this.dayProgressInSeconds >= this.dayLengthInSeconds);
	},
	
	dayProgress() {
		return (this.dayProgressInSeconds / this.dayLengthInSeconds) * 100;
	},
	
	canAddTask() {
		return this.timeLeftInTheDay >= MIN_TASK_LENGTH;
	},
	
	timeLeftInTheDay() {
		return (this.dayLengthInMinutes - this.totalTaskTime);
	},
	
	dayLengthInMinutes() {
		return this.dayLength * 60;
	},
	
	dayLengthInSeconds() {
		return this.dayLengthInMinutes * 60;
	},
	
	totalTaskTime() {
		return this.tasks.map(task => task.duration).reduce((prev, curr) => (prev += curr), 0);
	},
	
	dayIsFull() {
		return this.totalTaskTime >= this.dayLengthInMinutes;
	},
	
});

const setState = (key, value) => {
	if (key == undefined || value == undefined) return;
	if (typeof value === 'object') {
		if (Array.isArray(value)) {
			state[key].splice(0, state[key].length, ...value);
		} else {
			state[key] = { ...value };
		}
	} else {
		state[key] = value;
	}
};

const loadState = (key) => {
	let state = {};
	let json = localStorage?.getItem('GoGoState');
	if (json) {
		state = JSON.parse(json);
	}
	if (key) {
		return state[key];
	}
	return state;
};

const saveState = (key, value) => {
	let state = loadState();
	state[key] = value;
	localStorage?.setItem('GoGoState', JSON.stringify(state));
};

export default {
	install(Vue) {
		Vue.mixin({
			beforeCreate: function() {
				this.$state = state;
			}
		});
	}
};

export {
	state,
	setState,
	loadState,
	saveState
}