<template>
	<div :style="style">
		<slot />
	</div>
</template>

<script>
	export default {
		name: "GoGridColumn",
		props: {
			width: String,
			alignment: {
				type: String,
				default: 'left',
			},
			padding: String,
			paddingTop: String,
			paddingBottom: String,
			paddingLeft: String,
			paddingRight: String,
			horizontalPadding: String,
			verticalPadding: String,
		},
		computed: {
			style() {
				return {
					width: this.width,
					paddingTop: this.paddingTop || this.verticalPadding || this.padding,
					paddingBottom: this.paddingBottom || this.verticalPadding || this.padding,
					paddingLeft: this.paddingLeft || this.horizontalPadding || this.padding,
					paddingRight: this.paddingRight || this.horizontalPadding || this.padding,
					textAlign: this.alignment,
				}
			}
		}
	}
</script>