<template>
  <div class="go-tooltip" :style="{ 'max-width': this.width }">
    <slot>
      <div class="go-tooltip__inner">
        <h5 v-if="title" class="go-tooltip__title">{{ title }}</h5>
        <p v-if="message" class="go-tooltip__message">{{ message }}</p>
      </div>
    </slot>
    <div data-popper-arrow class="arrow"></div>
  </div>
</template>

<script>
  
  import { createPopper } from '@popperjs/core';
  
  export default {
    name: 'GoTooltip',
    props: {
      component: [ Object, Function, String ],
      title: String,
      message: String,
      target: {
        type: HTMLElement,
      },
      position: {
        type: String,
        default: 'top',
      },
      width: {
        type: String,
        default: '180px',
      },
      programmatic: {
        type: Boolean,
        default: false,
      },
    },
    methods: {
      hide() {
        this.$destroy();
      }
    },
    beforeDestroy() {
      if (this.programmatic) {
        this.$el.parentNode.removeChild(this.$el);
      }
    },
    mounted() {
      if (this.programmatic) {
        document.body.appendChild(this.$el);
        this.popper = createPopper(this.target, this.$el, {
          placement: this.position,
          modifiers: [
            {
              name: 'arrow',
              options: {
                padding: 16,
              }
            },
          ]
        });
      }
    },
  }
  
</script>

<style lang="scss" scoped>
  
  @import '@/assets/css/colours.scss';
  
  $arrow-size: 1rem;
  $arrow-size-half: ($arrow-size * 0.5);
  
  .arrow,
  .arrow::before {
    position: absolute;
    width: $arrow-size;
    height: $arrow-size;
    border-radius: ($arrow-size / 8);
    background: inherit;
  }
  
  .arrow {
    visibility: hidden;
  }
  
  .arrow::before {
    visibility: visible;
    content: '';
    transform: rotate(45deg);
  }
  
  .go-tooltip {
    
    z-index: 100000;
    padding: 0.75rem 1rem;
    background-color: white;
    border-radius: 0.25rem;
    box-sizing: border-box;
    box-shadow:
      0 0.7px 2.2px rgba(0, 0, 0, 0.014),
      0 1.6px 5.3px rgba(0, 0, 0, 0.02),
      0 3px 10px rgba(0, 0, 0, 0.025),
      0 5.4px 17.9px rgba(0, 0, 0, 0.03),
      0 10px 33.4px rgba(0, 0, 0, 0.036),
      0 24px 80px rgba(0, 0, 0, 0.05)
    ;
    
    &__title {
      font-weight: 600;
      color: $slate;
      margin: 0 0 0.5rem 0;
    }
    
    &__message {
      margin: 0;
      font-size: 0.8rem;
      word-break: break-all;
    }
    
  }
  
  .go-tooltip[data-popper-placement^='top'] > .arrow {
    bottom: -$arrow-size-half;
  }
  
  .go-tooltip[data-popper-placement^='bottom'] > .arrow {
    top: -$arrow-size-half;
  }
  
  .go-tooltip[data-popper-placement^='left'] > .arrow {
    right: -$arrow-size-half;
  }
  
  .go-tooltip[data-popper-placement^='right'] > .arrow {
    left: -$arrow-size-half;
  }
  
</style>