import Vue from 'vue';
import GoTooltip from './GoTooltip';

const GoTooltipFactory = Vue.extend(GoTooltip);

let seed = 0;
let tooltip;

const Plugin = {
  
  install(Vue) {
    Vue.component(GoTooltip.name, GoTooltip);
    Vue.prototype.$tooltip = Plugin;
  },
  
  show(target, options) {
    tooltip = new GoTooltipFactory({
      propsData: {
        target,
        ...options,
        programmatic: true,
      }
    });
    tooltip.id = `tooltip${seed++}`;
    tooltip.$mount();
    return tooltip;
  },
  
}

export default Plugin;

export {
	GoTooltip
}