<template>
  <div>
    <div class="confetti-cannon"></div>
    <go-modal
      class="success"
      max-width="50%"
      centre-text>
      <div class="success__content">
        <h4 v-if="title" class="success__title">{{ title }}</h4>
        <div class="success__message">
          <p class="success__party-popper"></p>
          <p v-if="subtitle" class="success__subtitle">{{ subtitle }}</p>
          <p v-if="description" class="success__description">{{ description }}</p>
        </div>
        <go-button full-width @click="$emit('click')">Sweet! Thanks!</go-button>
      </div>
    </go-modal>
  </div>
</template>

<script>
  
  import GoButton from '../../components/GoButton.vue';
  import GoModal from '../../components/GoModal.vue';
  
  import party from 'party-js';
  
  export default {
    name: 'SuccessModal',
    props: {
      title: String,
      subtitle: String,
      description: String,
    },
    components: {
      GoButton,
      GoModal,
    },
    methods: {
      randomInt(min, max) {
        return Math.floor(Math.random() * (max - min + 1) + min);
      },
      randomParticleColour() {
        const colours = [
          party.Color.fromHex("#FFFFFF"),
          party.Color.fromHex("#6F86D6"),
          party.Color.fromHex("#9DE1A4"),
        ];
        return colours[this.randomInt(0, (colours.length - 1))];
      }
    },
    beforeDestroy() {
      clearInterval(this.confettiCannonIntervalId);
    },
    mounted() {
      this.confettiCannonIntervalId = setInterval(() => {
        
        if (this.confettiCannonCount >= 20) {
          clearInterval(this.confettiCannonIntervalId);
        }
        
        const confettiCannon = document.querySelector(".confetti-cannon");
        
        const newX = this.randomInt(32, (window.innerWidth - 32));
        const newY = this.randomInt(32, (window.innerHeight / 2));
        
        confettiCannon.style.top = `${newY}px`;
        confettiCannon.style.left = `${newX}px`;
        
        party.confetti(confettiCannon, {
          count: party.variation.range(15, 20),
          color: () => this.randomParticleColour(),
        });
        
        this.confettiCannonCount++;
        
      }, 500);
    },
    data() {
      return {
        confettiCannonCount: 0,
        confettiCannonIntervalId: null,
      }
    }
  }
  
</script>

