    height: 32px;
    background-color: white;
    border-radius: 16px;
    opacity: 0;
  }
  
  .success {
    
    &__content {
      max-width: 320px;
      margin: 0 auto;
    }
    
    &__title {
      color: $blue;
      margin: 0;
    }
    
    &__message {
      text-align: center;
      margin: 3rem 0;
    }
    
    &__party-popper {
      font-size: 4rem;
      animation: tada 1250ms ease 8;
      margin: 1.5rem 0;
    }
    
    &__subtitle {
      font-size: 1.5rem;
    }
    
    &__description {
      
    }
    
  }
  
  // Tada animation taken from https://animate.style/
  
  @keyframes tada {
    from {
      transform: scale3d(1, 1, 1);
    }

    10%,
    20% {
      transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
    }

    30%,
    50%,
    70%,
    90% {
      transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
    }

    40%,
    60%,
    80% {
      transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
    }

    to {
      transform: scale3d(1, 1, 1);
    }
  }
  
</style>