import topHatPanda from './artists/tophatpanda';
import raimu from './artists/raimu';

const allArtists = [
  topHatPanda,
  raimu,
];

const allTracks = allArtists.flatMap((a) => {
  const { name, profilePicture } = a;
  const artist = { name, profilePicture };
  return a.tracks.map((track) => ({
    ...track,
    artist
  }));
});

export default allTracks;