<template>
	<div class="go-timeline-card"
		:class="{
			'go-timeline-card--engaged': engaged,
			'go-timeline-card--complete': data.complete
		}"
		:style="{
			width: getWidth(data.duration)
		}"
		@mouseenter="onHoverCard"
		@mousemove="onTravelCard"
		@mouseleave="onLeaveCard">
		<div class="go-timeline-card__inner">
			<go-tooltip
				ref="cardInfo"
				class="go-timeline-card__info"
				:title="data.title"
				:message="$options.filters.formatDuration(data.duration)"
			/>
			<div class="go-timeline-card__content">
				<input class="title" v-model="data.title" type="text" placeholder="What is the task?">
				<div class="footer">
					<span class="duration">{{ data.duration | formatDuration }}</span>
				</div>
			</div>
			<go-tooltip ref="cardToolbar" class="go-timeline-card__toolbar">
				<go-button class="action-button drag-handle" type="link" size="large">
					<template #leftIcon><img src="/img/icons/move.svg"></template>
				</go-button>
				<go-popover ref="popover" position="top" @show="engaged = true" @hide="engaged = false" width="400px">
					<go-button class="action-button delete-task" type="link" size="large">
						<template #leftIcon><img src="/img/icons/trashcan.svg"></template>
					</go-button>
					<template #title>Please Confirm</template>
					<template #content><p style="padding: 1rem 0;">Are you sure you want to delete this task?</p></template>
					<template #footer>
						<go-button type="light" size="small" @click="onClickDeleteTask">YES, DELETE TASK</go-button>
					</template>
				</go-popover>
				<go-button class="action-button complete-task" type="link" size="large" @click="onClickCompleteTask">
					<template #leftIcon>
						<img v-if="data.complete" src="/img/icons/checkmark-success.svg">
						<img v-else src="/img/icons/checkmark.svg">
					</template>
				</go-button>
			</go-tooltip>
			<span class="resize-handle"></span>
		</div>
	</div>
</template>

<script>
	
	import { createPopper } from '@popperjs/core';
	
	import party from 'party-js';
	
	import GoButton from './GoButton';
	import GoPopover from './GoPopover.vue';
	import GoTooltip from './GoTooltip/GoTooltip.vue';
	
	export default {
		name: 'GoTimelineCard',
		props: {
			data: {
				type: Object,
				default: () => ({}),
			},
			dayLength: Number,
		},
		components: {
			GoButton,
			GoPopover,
			GoTooltip
		},
		methods: {
			async onHoverCard(e) {
				await this.cardInfo.update();
				await this.cardToolbar.update();
				this.$emit('hover', {
					element: this.$el,
					cardData: this.data,
					originalEvent: e,
				});
			},
			onTravelCard(e) {
				this.$emit('travel', {
					element: this.$el,
					cardData: this.data,
					originalEvent: e,
				});
			},
			onLeaveCard(e) {
				this.$emit('leave', {
					element: this.$el,
					cardData: this.data,
					originalEvent: e,
				});
			},
			onClickDeleteTask() {
				this.$refs.popover.hide();
				this.$emit('delete-task');
			},
			onClickCompleteTask() {
				party.sparkles(this.$el.querySelector('.complete-task'), {
					count: party.variation.range(5, 10),
					speed: party.variation.range(25, 100),
				});
				this.$emit('complete-task');
			},
			getWidth(itemWidth) {
				const width = ((itemWidth / (this.dayLength * 60)) * 100);
				return `calc(${width}% - 1rem)`;
			},
		},
		mounted() {
			
			const cardInner = this.$el.querySelector('.go-timeline-card__inner');
			const cardToolbar = this.$el.querySelector('.go-timeline-card__toolbar');
			const cardInfo = this.$refs.cardInfo.$el;
			
			this.cardInfo = createPopper(cardInner, cardInfo, {
				placement: 'top',
				strategy: 'fixed'
			});
			
			this.cardToolbar = createPopper(cardInner, cardToolbar, {
				placement: 'bottom',
				strategy: 'fixed'
			});
			
		},
		data() {
			return {
				engaged: false,
			}
		},
		watch: {
			'data.duration': {
				async handler() {
					await this.$nextTick();
					await this.cardInfo.update();
					await this.cardToolbar.update();
				}
			}
		}
	}
	
</script>

<style lang="scss" scoped>
	
	@import '@/assets/css/all.scss';
	
	$border-width: 2px;
	
	@mixin cardStyle($backgroundColour: #FFF, $borderColour: $blue, $textColour: $charcoal, $iconColour: $blue) {
		
		background-color: $backgroundColour;
		border-color: $borderColour;
		
		.resize-handle {
			background-color: $borderColour;
		}
		
		.title,
		.duration {
			color: $textColour;
		}
		
		.action-button {
			color: $iconColour;
		}
		
	}
	
	@mixin placeholderTextStyle($textColour: $charcoal) {
		::placeholder {
			color: $textColour;
			opacity: 1;
		}
		:-ms-input-placeholder {
			color: $textColour;
		}
		::-ms-input-placeholder {
			color: $textColour;
		}
	}
	
	.go-timeline-card {
		
		border: $border-width solid $light-gray;
		border-radius: 0.5rem;
		box-sizing: border-box;
		padding: 0.75rem;
		background-color: white;
		touch-action: none;
		user-select: none;
		cursor: pointer;
		transition: color 100ms, border-color 100ms, background-color 100ms;
		position: relative;
		margin: 0 0.5rem;
		
		&:last-of-type {
			&::after {
				content: none;
			}
		}
		
		&::after {
			content: "";
			display: block;
			width: 2px;
			height: 1rem;
			background-color: $light-gray;
			position: absolute;
			top: -1rem;
			right: calc(-0.5rem - 3px);
		}
		
		&__inner {
			display: grid;
			column-gap: 0.75rem;
			grid-template-columns: 1fr auto;
			width: 100%;
			height: 100%;
		}
		
		&__content {
			display: flex;
			flex-direction: column;
			overflow: hidden;
			justify-content: space-between;
		}
		
		&__info {
			opacity: 0;
			transition: opacity 100ms;
			pointer-events: none;
		}
		
		&__toolbar {
			opacity: 0;
			display: flex;
			pointer-events: none;
			transition: opacity 100ms;
			background-color: white;
			gap: 0.5rem;
			padding: 0.5rem 0.75rem;
		}
		
		.resize-handle {
			background-color: $light-gray;
			margin: 1rem 0;
			border-radius: $border-width;
			display: block;
			width: $border-width;
		}
		
		.title,
		.duration {
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			padding: 0.25rem;
		}
		
		.title {
			background-color: transparent;
			font-size: 0.9rem;
			color: #243746;
			border-radius: 0.25rem;
			border: 0;
			outline: 0;
			transition: background-color 100ms;
			&:focus {
				background-color: lighten($light-gray, 5%);
				border: 0;
				outline: 0;
			}
		}
		
		.duration {
			font-size: 0.7rem;
			font-weight: 600;
			color: $slate;
			text-transform: uppercase;
			align-items: flex-end;
			display: flex;
			height: 1.5rem;
			line-height: 1.5rem;
		}
		
		.footer {
			display: flex;
			align-items: center;
			column-gap: 1rem;
		}
		
		.action-button {
			padding: 0.25rem;
			img {
				width: 1.25rem;
				height: 1.25rem;
			}
		}
		
		.drag-handle {
			cursor: move;
		}
		
		&.show-info {
			.go-timeline-card__info {
				opacity: 1;
			}
		}
		
		&--engaged,
		&:hover {
			.go-timeline-card__toolbar {
				opacity: 1;
				pointer-events: painted;
				// display: flex;
			}
		}
		
		&--engaged,
		&.ghosting,
		&.dragging,
		&.resizing,
		&:hover {
			@include cardStyle();
			@include placeholderTextStyle();
		}
		
		&--active {
			@include cardStyle($purple, $purple, #FFFFFF, $charcoal);
			@include placeholderTextStyle($light-gray);
			input,
			input:focus {
				background: transparent !important;
			}
		}
		
		&--complete {
			@include cardStyle(#EBF0F8, #EBF0F8, #A1ABB9, #A1ABB9);
			@include placeholderTextStyle(#A1ABB9);
			.complete-task {
				color: $green !important;
			}
		}
		
		&.ghosting {
			opacity: 0.25;
			pointer-events: none;
		}
		
		&.dragging {
			border-color: $blue;
			opacity: 1 !important;
			transform: rotate(10deg);
			box-shadow:
				0 2.8px 2.2px rgba(0, 0, 0, 0.014),
				0 6.7px 5.3px rgba(0, 0, 0, 0.02),
				0 12.5px 10px rgba(0, 0, 0, 0.025),
				0 22.3px 17.9px rgba(0, 0, 0, 0.03),
				0 41.8px 33.4px rgba(0, 0, 0, 0.036),
				0 100px 80px rgba(0, 0, 0, 0.05)
			;
		}
		
		&.resizing {
			.go-timeline-card__info {
				opacity: 1;
			}
			.go-timeline-card__toolbar {
				opacity: 0;
				pointer-events: none;
			}
		}
		
		&.sortable-chosen,
		&.ghosting {
			.go-timeline-card__toolbar {
				opacity: 0 !important;
				pointer-events: none;
			}
		}
		
	}
	
</style>