<template>
  <div class="go-timeline-controls" :class="{ 'go-timeline-controls--disabled': disabled }">
    <go-slider-handle
      class="pan-control"
      :style="panControlStyles"
      ref="panControl"
      size="24px"
    />
    <vue-slider
      v-model="zoomHandlePositions"
      class="zoom-control"
      ref="zoomControl"
      :min="$constants.MIN_ZOOM_VALUE"
      :max="$constants.MAX_ZOOM_VALUE"
      :min-range="$constants.MIN_TIMELINE_ZOOM_PERCENT"
      :max-range="$constants.MAX_TIMELINE_ZOOM_PERCENT"
      :enable-cross="false"
      :tooltip="'none'"
      :height="8"
      :dot-size="[['16', '16'], ['16', '16']]"
      :lazy="false"
      :duration="0.1"
      @change="onChangeZoom"
    >
      <template #dot="{ index }">
        <go-slider-handle />
      </template>
    </vue-slider>
  </div>
</template>

<script>
  
  import VueSlider from 'vue-slider-component';
  import 'vue-slider-component/theme/default.css';
  
  import interact from 'interactjs';
  
  import GoSliderHandle from '../components/GoSliderHandle.vue';
  
  import {
    MIN_ZOOM_VALUE,
    MAX_ZOOM_VALUE
  } from '../constants';
  
  export default {
    name: 'GoTimelineControls',
    components: {
      VueSlider,
      GoSliderHandle,
    },
    props: {
      disabled: Boolean,
    },
    computed: {
      zoomHandlePositions: {
        set(values) {
          this.minX = Math.round(values[0]);
          this.maxX = Math.round(values[1]);
        },
        get() {
          return [ this.minX, this.maxX ];
        }
      },
      panControlStyles() {
        const [ min, max ] = this.zoomHandlePositions;
        const mid = ((max + min) / 2);
        return {
          left: `${((mid / this.$constants.MAX_TIMELINE_ZOOM_PERCENT) * 100)}%`
        }
      }
    },
    methods: {
      onChangeZoom(values, index) {
        const [ min, max ] = values;
        const diff = (max - min);
        this.$state.timelineZoom = ((this.$constants.MAX_TIMELINE_ZOOM_PERCENT - diff) + 100);
        this.$state.timelineActiveZoomHandle = index;
      },
      calcHandlePosition(event) {
        const [ minX, maxX ] = this.zoomHandlePositions;
        const { delta: { x: deltaX } } = event;
        const draggingLeft = deltaX <= 0;
        const draggingRight = deltaX >= 0;
        const reachedMinX = draggingLeft && (minX <= 0);
        const reachedMaxX = draggingRight && (maxX >= this.$constants.MAX_TIMELINE_ZOOM_PERCENT);
        if (reachedMinX) {
          this.minX = 0;
        } else if (reachedMaxX) {
          this.maxX = this.$constants.MAX_TIMELINE_ZOOM_PERCENT;
        } else {
          this.minX += Math.round(deltaX / 2);
          this.maxX += Math.round(deltaX / 2);
        }
      },
      reset() {
        this.$state.timelineZoom = 100;
        this.$set(this, 'zoomHandlePositions', [
          this.$constants.MIN_ZOOM_VALUE,
          this.$constants.MAX_ZOOM_VALUE
        ]);
      }
    },
    watch: {
      zoomHandlePositions(values) {
        this.$state.timelineZoomHandlePositions = values;
      }
    },
    mounted() {
      this.panControl = interact('.pan-control').draggable({
        lockAxis: 'x',
        modifiers: [
          interact.modifiers.restrictRect({
            endOnly: true
          })
        ],
        listeners: {
          end: (event) => {
            this.calcHandlePosition(event);
          },
          move: (event) => {
            this.calcHandlePosition(event);
          }
        }
      })
    },
    data() {
      return {
        minX: MIN_ZOOM_VALUE,
        maxX: MAX_ZOOM_VALUE,
      }
    }
  }
  
</script>

<style lang="scss" scoped>
  
  @import '@/assets/css/colours.scss';
  
  .go-timeline-controls {
    position: relative;
    display: flex;
    width: 100%;
    align-items: center;
    max-width: 400px;
    margin: 0 auto;
    &--disabled {
      opacity: 0.75;
      pointer-events: none;
    }
  }
  
  .zoom-control {
    width: 100% !important;
  }
  
  .pan-control {
    position: absolute;
    transform: translate(-50%);
    display: inline-flex;
    z-index: 100;
  }
  
  ::v-deep .vue-slider-process {
    background-color: $slate;
  }
  
</style>