import Vue from 'vue';

class MusicController {
  
  constructor() {
    
    this._state = new Vue.observable({
      currentTrackIndex: 0,
      tracks: [],
      paused: false,
      loading: false,
    });
    
    this._player = new Audio();
    this._player.autoplay = true;
    
    this._player.onplay = () => {
      this._state.paused = false;
    };
    
    this._player.onpause = () => {
      this._state.paused = true;
    };
    
    this._player.onended = (e) => {
      this.loadNextTrack();
    };
    
    this._player.onloadstart = () => {
      this._state.paused = true;
      this._state.loading = true;
    };
    
    this._player.onloadeddata = () => {
      this._state.paused = false;
      this._state.loading = false;
    };
    
  }
  
  loadNextTrack() {
    if (this.currentTrackIndex === (this.tracks.length - 1)) {
      this.currentTrackIndex = 0;
    } else {
      this.currentTrackIndex++;
    }
    this._player.src = this.currentTrack?.url;
    this._player.load();
  }
  
  toggleTrack() {
    if (this.paused) {
      this.play();
    } else {
      this.pause();
    }
  }
  
  play() {
    if (!this._player.src) {
      this._player.src = this.currentTrack?.url;
      this._player.load();
    }
    this._player.play();
  }
  
  pause() {
    this._player.pause();
  }
  
  get currentTrackIndex() {
    return this._state.currentTrackIndex;
  }
  
  set currentTrackIndex(newValue) {
    this._state.currentTrackIndex = newValue;
  }
  
  get currentTrack() {
    return this.tracks[this.currentTrackIndex];
  }
  
  get tracks() {
    return this._state.tracks;
  }
  
  set tracks(newValue) {
    this._state.tracks.splice(0, this._state.tracks.length, ...newValue);
  }
  
  get paused() {
    return this._state.paused;
  }
  
  set paused(newValue) {
    this._state.paused = newValue;
  }
  
  get volume() {
    return this._player.volume;
  }
  
  set volume(newValue) {
    this._player.volume = newValue;
  }
  
  get loading() {
    return this._state.loading;
  }
  
}

export default new MusicController();