<script>
  export default {
    name: 'GoSliderHandle',
    props: {
      size: {
        type: String,
        default: '16px',
      }
    },
    computed: {
      style() {
        return {
          width: this.size,
          height: this.size,
        }
      }
    }
  }
</script>

