<template>
	
	<div class="view settings-view">
		
		<go-grid class="view-width-constraint">
			<div class="column-1">
				<div class="column-1__inner">
					<!-- Account Information -->
					<section>
						<h1>Account Information</h1>
						<go-account-info-button :type="$state.user.accountType" :name="$state.user.displayName" @log-out="onClickLogOut" />
					</section>
					<!-- Subscription -->
					<section class="border">
						<h1>Subscription</h1>
						<go-subscription @subscribe="onClickSubscribe" @cancel="onClickCancelSubscription"/>
					</section>
					<!-- General / Other -->
					<section class="border">
						<h1>General</h1>
						<div style="display: flex; justify-content: space-between;">
							<span>Contact us on Email</span>
							<span><a :href="`mailto:${$constants.SUPPORT_EMAIL_ADDRESS}`">{{ $constants.SUPPORT_EMAIL_ADDRESS }}</a></span>
						</div>
					</section>
					<div class="footer">
						<go-button size="small" type="link" @click="showLegalsModal = true">Privacy Policy and Terms of Service</go-button>
						<span>{{ $constants.COPYRIGHT }}</span>
					</div>
				</div>
			</div>
			<div class="column-2">
				<illustration />
			</div>
		</go-grid>
		
		<div class="fixed-top-right">
			<go-button class="close-button" type="ghost" right-icon="x" @click="onClickCloseButton" />
		</div>
		
		<go-legals-modal
			v-show="showLegalsModal"
			@click-background="showLegalsModal = false"
		/>
		
		<router-view v-if="showModal" />
		
	</div>
	
</template>

<script>
	
	import GoAccountInfoButton from '../components/GoAccountInfoButton.vue';
	import GoButton from '../components/GoButton.vue';
	import GoGrid from '../components/GoGrid.vue';
	import GoSubscription from '../components/GoSubscription.vue';
	
	import GoLegalsModal from '../components/Modals/Legals.vue';
	
	import Illustration from '../components/Illustration.vue';
	
	export default {
		name: 'Settings',
		components: {
			GoAccountInfoButton,
			GoButton,
			GoGrid,
			GoSubscription,
			GoLegalsModal,
			Illustration
		},
		methods: {
			onClickSubscribe() {
				this.$router.push({ name: 'subscribe' });
			},
			async onClickCancelSubscription(callback) {
				await this.$subscriptionController.unsubscribe();
				callback();
			},
			async onClickLogOut() {
				await this.$authController.signOut();
				this.$router.replace({ name: 'login' });
			},
			onClickCloseButton() {
				this.$router.push({ name: 'home' });
			}
		},
		watch: {
			$route: {
				immediate: true,
				handler() {
					this.showModal = this.$route?.meta?.modal ?? false;
				},
			}
		},
		data() {
			return {
				showModal: false,
				showLegalsModal: false,
			}
		}
	}
	
</script>

