import Vue from 'vue';
import GoToast from './GoToast';

const GoToastFactory = Vue.extend(GoToast);

let seed = 0;
let toast;

const Plugin = {
  
  install(Vue) {
    Vue.component(GoToast.name, GoToast);
    Vue.prototype.$toast = Plugin;
  },
  
  show(options) {
    toast = new GoToastFactory({
      propsData: {
        ...options,
      }
    });
    toast.id = `toast${seed++}`;
    toast.$mount();
    toast.show();
    return toast;
  },
  
}

export default Plugin;

export {
	GoToast
}