<template>
	<div
		ref="slider"
		class="go-slider"
		:style="style"
		:class="[ handleSize, {
			'go-slider--disabled': disabled,
		} ]">
		
	</div>
</template>

<script>
	
	import interact from 'interactjs';
	
	export default {
		
		name: 'GoSlider',
		
		props: {
			width: {
				type: [ Number, String ],
				default: 100,
			},
			defaultValue: {
				type: Number,
				default: 0,
			},
			minValue: {
				type: Number,
				default: 0,
			},
			maxValue: {
				type: Number,
				default: 1,
			},
			handleSize: {
				type: String,
				default: 'medium',
			},
			disabled: {
				type: Boolean,
				default: false,
			},
		},
		
		computed: {
			sliderValue() {
				return Math.abs(this.positionFraction * this.maxValue);
			},
			gridSize() {
				return (this.width / this.maxValue);
			},
			style() {
				const percentage = (this.positionFraction * 100).toFixed() + '%';
				return {
					width: typeof this.width === 'string' ? this.width : `${this.width}px`,
					background: `linear-gradient(90deg, rgba(111,134,214,1) ${percentage}, rgba(225,231,240,1) ${percentage})`,
					paddingLeft: percentage,
				}
			}
		},
		
		methods: {
			setPosition(newValue) {
				let value = newValue;
				let newFractionValue = (value / this.maxValue);
				if (value <= this.minValue) {
					newFractionValue = (this.minValue / this.maxValue);
				} else if (value >= this.maxValue) {
					newFractionValue = (value / this.maxValue);
				}
				this.positionFraction = newFractionValue;
			},
			enableDisable() {
				this.slider.draggable(!this.disabled);
			}
		},
		
		mounted() {
			
			this.setPosition(this.defaultValue);
			
			this.slider = interact(this.$el).draggable({
				origin: 'self',
				lockAxis: 'x',
				modifiers: [
					interact.modifiers.restrict({
						restriction: 'self'
					}),
					interact.modifiers.snap({
						targets: [
							interact.snappers.grid({ x: this.gridSize, y: this.gridSize })
						],
						relativePoints: [
							{ x: 0.5, y: 0.5 }
						],
					})
				],
				listeners: {
					move: (event) => {
						if (!this.disabled) {
							const newSliderValue = Math.abs(Math.round(event.pageX / this.gridSize));
							this.setPosition(newSliderValue);
						}
					}
				}
			})
			
		},
		
		watch: {
			defaultValue: {
				handler(value) {
					this.setPosition(value);
				}
			},
			positionFraction: {
				handler() {
					this.$emit('change', this.sliderValue);
				}
			},
			disabled: {
				handler() {
					this.enableDisable();
				}
			},
		},
		
		data() {
			return {
				slider: null,
				positionFraction: 0,
			}
		}
		
	}
	
</script>

<style lang="scss" scope>
	
	@import '@/assets/css/all.scss';
	
	$slider-height: 0.4rem;
	
	$handle-size: 1.5rem;
	$handle-size-small: 1rem;
	
	.go-slider {
		position: relative;
		height: $slider-height;
		background-color: $light-gray;
		border-radius: $slider-height;
		box-sizing: border-box;
		-ms-touch-action: none;
				touch-action: none;
		z-index: 1;
		&--disabled {
			opacity: 0.5;
			cursor: not-allowed !important;
		}
	}

	.go-slider:before {
		content: "";
		display: block;
		position: relative;
		width: $handle-size;
		height: $handle-size;
		top: -(($handle-size - ($slider-height)) / 2);
		margin-left: -($handle-size / 2);
		border-radius: 0.5rem;
		background-color: white;
		box-shadow:
			0 0.2px 0.6px rgba(0, 0, 0, 0.02),
			0 0.5px 1.3px rgba(0, 0, 0, 0.028),
			0 1px 2.5px rgba(0, 0, 0, 0.035),
			0 1.8px 4.5px rgba(0, 0, 0, 0.042),
			0 3.3px 8.4px rgba(0, 0, 0, 0.05),
			0 8px 20px rgba(0, 0, 0, 0.07)
		;
	}
	
	.go-slider.small:before {
		width: $handle-size-small;
		height: $handle-size-small;
		top: -(($handle-size-small - ($slider-height)) / 2);
		margin-left: -($handle-size-small / 2);
	}
	
</style>