import firebase from 'firebase/app';
import 'firebase/functions';

import { state } from '../state';

const formatInterval = (interval, step) => {
	const isDaily = (interval === 'day');
	const isWeekly = (interval === 'week');
	const isMonthly = (interval === 'month');
	if (isDaily) {
		return 'Per Day';
	} else if (isWeekly) {
		return 'Per Week';
	} else if (isMonthly) {
		switch (step) {
			case 3:
				return 'Per Quarter';
			case 6:
				return 'Every 6 Months';
			case 12:
				return 'Per Year';
			default:
				return 'Per Month';
		}
	}
};

class SubscriptionController {
	
	async loadSubscriptionInfo() {
		const response = await firebase.functions().httpsCallable('getSubscriptionInfo')();
		return {
			id: response.data.id,
			currency: response.data.currency.toUpperCase(),
			amount: (response.data.unit_amount / 100),
			interval: formatInterval(response.data?.recurring?.interval, response.data?.recurring?.interval_count),
		};
	}
	
	async unsubscribe() {
		const response = await firebase.functions().httpsCallable('unsubscribe')();
		state.userSubscription = (response?.data) ? response.data : {};
	}
	
}

export default new SubscriptionController();