<template>
	<go-button class="account-info-button" size="medium" type="ghost" full-width @click="$emit('click')">
		<div class="account-info-button__inner">
			<img v-if="type" class="icon" :src="`/img/icons/${type}.svg`" :alt="name">
			<div class="account-info-button__text">
				<span class="account-type">{{ accountType }}</span>
				<span class="account-name" v-if="name">{{ name }}</span>
			</div>
			<go-button type="link" size="small" full-width @click="$emit('log-out')">LOG OUT</go-button>
		</div>
	</go-button>
</template>

<script>
	
	import GoButton from './GoButton.vue'
	
	export default {
		name: 'GoAccountInfoButton',
		components: { GoButton },
		props: {
			type: {
				type: String,
				default: '',
			},
			name: {
				type: String,
				default: '',
			}
		},
		computed: {
			accountType() {
				return `${this.type} Account`.toUpperCase();
			}
		}
	}
	
</script>

