<template>
  
  <div v-if="$state.trialAccountExpired" class="view 404-view">
    <router-view v-if="showModal" />
  </div>
  
  <div v-else class="view home-view">
    
    <img style="display: none; width: 1152px; height: 64px; position: absolute;" src="~@/assets/character-running.png">
    <img style="display: none; width: 64px; height: 64px; position: absolute;" src="~@/assets/smoke.gif">
    
    <go-background :animated="$state.dayHasStarted">
      <go-avatar :animated="$state.dayHasStarted" flipped />
    </go-background>
    
    <div class="timeline-container">
      
      <go-timeline v-model="$state.tasks" :progress="$state.dayProgress" :day-length="$state.dayLength"/>
      
      <go-grid class="timeline-actions" no-gutter>
        
        <go-grid-column width="8rem" padding="0.5rem" />
        
        <go-grid-column width="8rem" padding="0.5rem" />
        
        <go-grid-column width="auto" padding="1.25rem">
          <go-timeline-controls ref="timelineControls" :disabled="disableTimelineControls" />
        </go-grid-column>
        
        <go-grid-column width="8rem" padding="0.5rem" alignment="center">
          <go-popover ref="dayLength" position="top-end" width="auto" @hide="onHideDayLengthPopover">
            <go-button type="link" size="small">DAY LENGTH</go-button>
            <template #title>Set Length of Day</template>
            <template #content>
              <div style="padding: 0.5rem 0; margin-bottom: 1rem;">
                <div class="day-length">
                  <h1 class="time">{{ dayLength }}</h1><span class="time-unit">HRS</span>
                </div>
                <!-- {{ dayLength }} / {{ minimumDayLength }} -->
                <go-slider
                  :width="300"
                  :default-value="dayLength"
                  :min-value="minimumDayLength"
                  :max-value="$constants.MAX_DAY_LENGTH"
                  @change="onUpdateDayLengthSlider"/>
              </div>
              <!-- <go-button
                type="link"
                size="small"
                :disabled="disableChangeDayLengthButton"
                @click="onClickChangeDayLengthButton">CHANGE DAY LENGTH</go-button> -->
            </template>
          </go-popover>
        </go-grid-column>
        
        <go-grid-column width="8rem" padding="0.5rem" alignment="center">
          <go-popover ref="resetDay" position="top-end">
            <go-button type="link" size="small">RESET DAY</go-button>
            <template #title>Reset The Day</template>
            <template #content>
              <p style="padding: 1rem;">Reseting your day will remove all your tasks and set the timer back to 0</p>
            </template>
            <template #footer>
              <go-button size="small" @click="onClickResetDay">RESET DAY</go-button>
            </template>
          </go-popover>
        </go-grid-column>
        
      </go-grid>
      
    </div>
    
    <div class="fixed-top-left">
      <img class="logo" src="/img/logo.svg" alt="">
    </div>
    
    <div class="fixed-top-right">
      <go-music-player :controller="$musicController" />
      <go-popover ref="referrals" position="auto" width="640px">
        <go-circle-button class="button" icon="boxes"/>
        <template #content>
          <go-referrals @click-get-paid="showAddPayPalModal = true"/>
        </template>
      </go-popover>
      <go-circle-button class="button" icon="user" @click="onClickAccountButton" />
    </div>
    
    <go-modal
      v-if="showReferralsModal"
      padding="2rem"
      max-width="640px"
      @click-background="onClickDismissReferralsModal">
      <go-referrals @click-get-paid="showAddPayPalModal = true"/>
    </go-modal>
    
    <go-referrals-add-paypal
      v-if="showAddPayPalModal"
      @cancelled="showAddPayPalModal = false"
      @saved="showAddPayPalModal = false"
    />
    
    <go-success-modal
      v-if="showSuccessfulReferralModal"
      title="Referral Successful"
      subtitle="Your referral was successful!"
      description="Your friend(s) subscribed, weve added the count to your referral tab so you can check how many people referred succesfully."
      @click="onClickDismissSuccessfulReferralModal"
    />
    
    <go-success-modal
      v-if="$state.dayHasFinished"
      title="Day Complete"
      subtitle="Good job! Time to take it easy and rest up!"
      @click="onClickResetDay"
    />
    
  </div>
  
</template>

<script>
  
  import { loadState, saveState } from '../state';
  import { DELAY_BEFORE_SHOWING_INITIAL_REFERRALS_MODAL } from '../constants';
  
  import GoAvatar from '../components/GoAvatar.vue';
  import GoBackground from '../components/GoBackground.vue';
  import GoButton from '../components/GoButton.vue';
  import GoGrid from '../components/GoGrid.vue';
  import GoGridColumn from '../components/GoGridColumn.vue';
  import GoPopover from '../components/GoPopover';
  import GoSlider from '../components/GoSlider.vue';
  import GoTimeline from '../components/GoTimeline.vue';
  import GoTimelineControls from '../components/GoTimelineControls.vue';
  import GoMusicPlayer from '../components/GoMusicPlayer.vue';
  import GoCircleButton from '../components/GoCircleButton.vue';
  import GoModal from '../components/GoModal.vue';
  
  import GoReferrals from '../components/GoReferrals.vue';
  import GoReferralsAddPaypal from '../components/GoReferralsAddPayPal.vue';
  
  import GoSuccessModal from '../components/Modals/Success.vue';
  
  export default {
    name: 'Home',
    components: {
      GoAvatar,
      GoBackground,
      GoButton,
      GoGrid,
      GoGridColumn,
      GoPopover,
      GoSlider,
      GoTimeline,
      GoTimelineControls,
      GoMusicPlayer,
      GoCircleButton,
      GoModal,
      GoReferrals,
      GoReferralsAddPaypal,
      GoSuccessModal
    },
    computed: {
      minimumDayLength() {
        if (this.$state.tasks.length) {
          return Math.ceil((this.$state.tasks.length * this.$constants.MIN_TASK_LENGTH) / 60);
        }
        return this.$constants.MIN_DAY_LENGTH;
      },
      trialHasExpired() {
        return this.$state.userSubscriptionLoaded && this.$state.trialAccountExpired;
      },
      isTrialExpiredRoute() {
        return this.$route.name === 'trial-expired';
      },
      disableChangeDayLengthButton() {
        return this.$state.dayLength === this.dayLength;
      },
      disableTimelineControls() {
        return !this.$state.dayHasStarted;
      }
    },
    methods: {
      onHideDayLengthPopover() {
        this.dayLength = this.$state.dayLength;
      },
      onUpdateDayLengthSlider(e) {
        
        const newDayLength = e;
        const newDayLengthInMinutes = (newDayLength * 60);
        
        const totalTaskDurationInHours = +Number(this.$state.totalTaskTime / 60).toFixed(2);
        
        const round = (number, increment, offset) => {
          return Math.ceil((number - offset) / increment) * increment + offset;
        }
        
        const sum = (list) => {
          return list.reduce((total, length) => (total += length), 0);
        }
        
        const diff = (greater, smaller) => {
          return (greater - smaller);
        };
        
        if (newDayLength < totalTaskDurationInHours) {
          const multiplier = (newDayLength / totalTaskDurationInHours);
          // Apply the multiplier to all of the task durations.
          const modified = this.$state.tasks.map((task) => {
            let newDuration = (task.duration * multiplier);
            let roundedDuration = round(newDuration, 5, 0);
            if (roundedDuration < this.$constants.MIN_TASK_LENGTH) {
              roundedDuration = this.$constants.MIN_TASK_LENGTH;
            }
            return roundedDuration;
          });
          // Calculate the total time of the new task durations.
          // If the new total time is greater than the old total time,
          // find the longest running task and minus the difference.
          const newTotalTaskDuration = sum(modified)
          if (newTotalTaskDuration > newDayLengthInMinutes) {
            let longestTaskLength = Math.max(...modified);
            let longestTaskLengthIndex = modified.findIndex(taskLength => taskLength === longestTaskLength);
            modified[longestTaskLengthIndex] = (longestTaskLength - diff(newTotalTaskDuration, newDayLengthInMinutes));
          }
          // Update the tasks to use the new duration.
          this.$state.tasks.forEach((task, index) => task.duration = modified[index]);
        }
        this.dayLength = newDayLength;
        this.$state.dayLength = newDayLength;
      },
      onClickChangeDayLengthButton() {
        this.$state.dayLength = this.dayLength;
      },
      onClickAccountButton() {
        this.$router.push({ name: 'settings' })
      },
      onClickResetDay() {
        this.$refs.timelineControls.reset();
        this.$refs.resetDay.hide();
        this.$state.tasks.splice(0);
      },
      onClickDismissSuccessfulReferralModal() {
        this.showSuccessfulReferralModal = false;
      },
      onClickDismissReferralsModal() {
        this.showReferralsModal = false;
        saveState('dismissedInitialReferralModal', true);
      },
    },
    watch: {
      '$route': {
        immediate: true,
        handler() {
          this.showModal = this.$route?.meta?.modal ?? false;
          if (this.trialHasExpired && !this.isTrialExpiredRoute) {
            this.$router.replace({ name: 'trial-expired' });
          }
        },
      }
    },
    async mounted() {
      
      const dismissedInitialReferralModal = loadState('dismissedInitialReferralModal') ?? false;
      
      this.unwatchUserSubscriptionLoaded = this.$watch('$state.userSubscriptionLoaded', (loaded) => {
        if (this.trialHasExpired) {
          this.$router.replace({ name: 'trial-expired' });
        }
      }, {
        immediate: true,
      });
      
      if (!dismissedInitialReferralModal) {
        this.showReferralsModalTimer = setTimeout(() => {
          this.showReferralsModal = true;
        }, DELAY_BEFORE_SHOWING_INITIAL_REFERRALS_MODAL);
      }
      
    },
    beforeDestroy() {
      this.unwatchUserSubscriptionLoaded();
      if (this.showReferralsModalTimer) {
        clearTimeout(this.showReferralsModalTimer);
      }
    },
    data() {
      return {
        dayLength: this.$state.dayLength,
        showModal: false,
        showSuccessfulReferralModal: false,
        showReferralsModal: false,
        showReferralsModalTimer: null,
        showAddPayPalModal: false,
      }
    }
  }
  
</script>

<style lang="scss" scoped>
  
  @import '@/assets/css/all.scss';
  
  .home-view {
    display: grid;
    grid-template-rows: 1fr auto;
  }
  
  .timeline-container {
    display: grid;
    background-color: white;
    grid-template-rows: 1fr auto;
    padding: 1rem 0 0 0;
    box-sizing: border-box;
    z-index: 100;
  }
  
  .timeline-actions {
    padding: 0 2rem;
  }
  
  .day-length {
    display: inline-flex;
    column-gap: 0.25rem;
    margin: 1rem 0 2rem 0;
    .time {
      font-size: 3rem;
      line-height: 2.5rem;
      margin: 0;
    }
    .time-unit {
