import Vue from 'vue';
import VueRouter from 'vue-router';

import MissingRoute from '../views/404.vue';
import Login from '../views/Login.vue';
import Home from '../views/Home.vue';
import Settings from '../views/Settings.vue';
import Subscribe from '../views/Subscribe.vue';
import TrialExpired from '../views/TrialExpired.vue';

import { state } from '../state';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'login',
    component: Login,
    meta: {
      unauthenticated: true,
    }
  },
  {
    path: '/home',
    name: 'home',
    component: Home,
    children: [
      {
        path: 'trial-expired',
        name: 'trial-expired',
        component: TrialExpired,
        props: true,
        meta: {
          modal: true,
        }
      }
    ]
  },
  {
    path: '/settings',
    name: 'settings',
    component: Settings,
    children: [
      {
        path: 'subscribe',
        name: 'subscribe',
        component: Subscribe,
        props: true,
        meta: {
          modal: true,
        }
      }
    ]
  },
  {
    path: '*',
    component: MissingRoute,
    meta: {
      unauthenticated: true,
    }
  }
]

const router = new VueRouter({
  routes,
});

router.beforeEach((to, from, next) => {
  if (to?.meta?.unauthenticated) {
    next();
  } else {
    if (state?.user?.id) {
      next();
    } else {
      next(routes[0]);
    }
  }
});

export default router;