import firebase from 'firebase/app';
import 'firebase/auth';

import * as constants from './constants';
import authController from './controllers/auth-controller';
import subscriptionController from './controllers/subscription-controller';
import taskController from './controllers/task-controller';
import musicController from './controllers/music-controller';

import { AUTH_PROVIDER_TO_ACCOUNT_TYPE } from './constants';
import { state, setState, loadState, saveState } from './state';
import { watch } from './utils';

class GoGo {
	
	install(Vue) {
		const that = this;
		Vue.mixin({
			beforeMount: function() {
				this.$gogo = that;
				this.$constants = constants;
				this.$authController = authController;
				this.$subscriptionController = subscriptionController;
				this.$taskController = taskController;
				this.$musicController = musicController;
			}
		});
	}
	
	async loadUserData() {
		
		const response = await firebase.functions().httpsCallable('getUserProfileData')();
		
		state.userSubscription = response?.data?.subscription ?? {};
		
		state.userSubscriptionLoaded = true;
		
		state.userReferrals = response?.data?.referrals ?? [];
		
		state.userSettings = response?.data?.settings ?? {};
		
	}
	
	async init() {
		
		const savedState = loadState();
		
		let timeElapsedBetweenSessions = 0;
		
		if (process.env.NODE_ENV === 'development') {
			musicController.tracks = require('./music/development/index').default;
		} else {
			musicController.tracks = require('./music/production/index').default;
		}
		
		if (savedState.sessionClosed) {
			let now = new Date().getTime();
			let past = savedState.sessionClosed;
			let timeElapsed = (now - past);
			let timeElapsedInSeconds = Math.floor(timeElapsed / 1000);
			timeElapsedBetweenSessions += timeElapsedInSeconds;
		}
		
		setState('dayLength', savedState.dayLength);
		setState('dayProgressInSeconds', (savedState.dayProgress + timeElapsedBetweenSessions));
		setState('tasks', savedState.tasks);
		
		watch({ state }, 'state.dayLength', (newValue) => {
			saveState('dayLength', newValue);
		}, {
			immediate: true,
		});
		
		watch({ state }, 'state.dayProgressInSeconds', (newValue) => {
			saveState('dayProgress', newValue);
		}, {
			immediate: true,
		});
		
		watch({ state }, 'state.tasks', (newValue) => {
			saveState('tasks', newValue);
		}, {
			immediate: true,
			deep: true,
		});
		
		await firebase.initializeApp({
			apiKey: process.env.VUE_APP_FIREBASE_API_KEY,
			projectId: process.env.VUE_APP_FIREBASE_PROJECT_ID,
			authDomain: process.env.VUE_APP_FIREBASE_AUTH_DOMAIN,
			databaseURL: process.env.VUE_APP_FIREBASE_DATABASE_URL,
		});
		
		firebase.auth().setPersistence(firebase.auth.Auth.Persistence.LOCAL);
		
		firebase.auth().onAuthStateChanged(async (user) => {
			
			if (user) {
				
				state.authenticating = true;
				
				const { uid, displayName, email, providerData } = user;
				const accountType = AUTH_PROVIDER_TO_ACCOUNT_TYPE[providerData[0].providerId];
				
				state.user = {
					email,
					accountType,
					id: uid,
					displayName: displayName ?? email,
					registrationDate: user?.metadata?.creationTime,
				};
				
				await this.loadUserData();
				
			} else {
				
				state.authenticating = false;
				
				state.user = {};
				
			}
			
		});
		
	}
	
}

export default new GoGo();