<template>
  <div class="go-music-player-wrapper">
    <div class="go-music-player shadow" :class="{ 'go-music-player--closed': !open }">
      <div class="go-music-player__inner">
        <div class="go-music-player__controls">
          <go-button
            type="link"
            size="large"
            :left-icon="toggleMusicIcon"
            full-width
            :loading="controller.loading"
            @click="onClickToggleMusic" />
          <go-button
            type="link"
            size="large"
            left-icon="skip-next"
            full-width
            @click="onClickNextTrack" />
          <div>
            <go-slider
              :width="64"
              handle-size="small"
              :default-value="this.defaultVolume"
              :min-value="this.minVolume"
              :max-value="this.maxVolume"
              @change="onChangeVolume"/>
          </div>
        </div>
        <img class="go-music-player__music-note" src="/img/icons/audio-note.svg" @click="onClickMusicNote">
      </div>
    </div>
    <transition name="artist-info-fade" mode="out-in">
    <div v-if="open">
      <transition name="artist-info-swipe" mode="out-in">
        <div class="go-music-player__track-info" :key="currentTrack.name">
          <img class="artist-picture shadow" :src="currentTrack.artist.profilePicture">
          <div>
            <h6 class="artist-name">{{ artistName }}</h6>
            <p class="track-name">{{ currentTrack.name }}</p>
          </div>
        </div>
      </transition>
    </div>
    </transition>
  </div>
</template>

<script>
  
  import GoButton from './GoButton.vue';
  import GoSlider from './GoSlider';
  
  export default {
    name: 'GoMusicPlayer',
    props: {
      controller: Object,
    },
    components: {
      GoButton,
      GoSlider
    },
    computed: {
      artistName() {
        let artistName = this.currentTrack.artist.name;
        if (this.currentTrack.featuring) {
          artistName += ` X ${this.currentTrack.featuring}`;
        }
        return artistName;
      },
      currentTrack() {
        return this.controller.currentTrack;
      },
      toggleMusicIcon() {
        return this.controller.paused ? 'play' : 'pause';
      },
    },
    methods: {
      onClickNextTrack() {
        this.controller.loadNextTrack();
      },
      onClickToggleMusic() {
        this.controller.toggleTrack();
      },
      onChangeVolume(newVolume) {
        this.controller.volume = (newVolume / 100);
      },
      onClickMusicNote() {
        this.open = !this.open;
        if (this.open) {
          this.controller.play();
        } else {
          this.controller.pause();
        }
      }
    },
    mounted() {
      this.defaultVolume = (this.controller.volume * 100);
      this.controller.play();
    },
    data() {
      return {
        open: true,
        defaultVolume: 0,
        minVolume: 0,
        maxVolume: 100,
      }
    }
  }
  
</script>

