<template>
  <illustration title="Let's Get You Paid!" @click-background="$emit('cancelled')">
    <template #subtitle>
      <p>In order to pay you, simply add your PayPal email.</p>
      <p>Payments are done automatically.</p>
    </template>
    <template #footer>
      <div class="go-input">
        <input
          v-model="emailAddress"
          type="email"
          placeholder="Enter Your PayPal Email"
        >
        <div v-show="validEmailAddress" class="go-input__icon-wrapper">
          <i class="bx bxs-check-circle go-input__icon"></i>
        </div>
      </div>
      <p class="information">Keep in mind, we send out payments on the 1st of every month, and only when your referral value is greater than $5 USD.</p>
      <go-button
        type="dark"
        @click="onClickAddPayPal"
        :loading="addingPayPal"
        :disabled="disableAddPayPalButton"
      >{{ saveButtonLabel }}</go-button>
      <go-button
        type="ghost"
        no-border
        @click="$emit('cancelled')"
      >CANCEL</go-button>
    </template>
  </illustration>
</template>

<script>
  
  import firebase from 'firebase/app';
  import 'firebase/functions';
  
  import Illustration from './Modals/Illustration.vue';
  import GoButton from './GoButton.vue';
  
  export default {
    
    name: 'GoReferralsAddPayPal',
    
    components: {
      Illustration,
      GoButton
    },
    
    computed: {
      addingEmail() {
        return this.$state.userSettings.paypalEmailAddress == '';
      },
      saveButtonLabel() {
        return this.addingEmail ? 'ADD PAYPAL' : 'CHANGE EMAIL';
      },
      validEmailAddress() {
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(this.emailAddress).toLowerCase());
      },
      disableAddPayPalButton() {
        if (this.addingEmail) {
          return !this.validEmailAddress;
        } else {
          if (this.emailAddress.length) {
            return !this.validEmailAddress;
          }
        }
        return false;
      }
    },
    
    methods: {
      
      async onClickAddPayPal() {
        
        this.addingPayPal = true;
        
        const paypalEmailAddress = this.emailAddress.trim();
        
        await firebase.functions().httpsCallable('saveUserSettings')({
          paypalEmailAddress
        });
        
        this.$set(this.$state.userSettings, 'paypalEmailAddress', paypalEmailAddress);
        
        this.$toast.show({
          title: 'Success!',
          message: 'Your PayPal email has been updated.',
        });
        
        this.$emit('saved');
        
      }
      
    },
    
    data() {
      return {
        addingPayPal: false,
        emailAddress: this.$state.userSettings.paypalEmailAddress ?? '',
      }
    }
    
  }
  
</script>

