import { TaskTimer } from 'tasktimer';
import { state } from '../state';
import { watch } from '../utils';
import * as constants from '../constants';

const ONE_SECOND = 1;
const ONE_SECOND_IN_MILLISECONDS = 1000;
const ONE_MINUTE = (ONE_SECOND * 60);

const DAY_PROGRESS_STEP = ONE_SECOND;
// const DAY_PROGRESS_STEP = (ONE_MINUTE * 5);

const TIMER_INTERVAL = ONE_SECOND_IN_MILLISECONDS;
const CHECK_TASK_STATUS_INTERVAL = ONE_MINUTE;
// const CHECK_TASK_STATUS_INTERVAL = ONE_SECOND;
const CHECK_DAY_FINISHED_INTERVAL = ONE_MINUTE;
// const CHECK_DAY_FINISHED_INTERVAL = ONE_SECOND;

class TaskTimerController {
	
	constructor() {
		
		this._timerRunning = false;
		
		this._timer = new TaskTimer(TIMER_INTERVAL);
		
		this._timer.add([
			{
				id: 'check-task-status',
				tickInterval: CHECK_TASK_STATUS_INTERVAL,
				totalRuns: 0,
				callback: () => {
					this.resolveTaskStatus();
				}
			},
			{
				id: 'check-day-finished',
				tickInterval: CHECK_DAY_FINISHED_INTERVAL,
				totalRuns: 0,
				callback: () => {
					if (state.dayHasFinished) {
						this.stopTimer();
					}
				}
			},
		])
		
		this._timer.on('tick', () => {
			state.dayProgressInSeconds += DAY_PROGRESS_STEP;
		});
		
		watch({ tasks: state.tasks }, 'tasks', (newValue) => {
			if (newValue && newValue.length) {
				this.startTimer();
			} else {
				this.resetTimer();
			}
		}, {
			immediate: true,
			deep: true,
		});
		
	}
	
	resolveTaskStatus() {
		/* state.tasks.reduce((startTime, task, index) => {
			const duration = (task.duration * 60);
			const endTime = (startTime + duration);
			const taskIsActive = (state.dayProgressInSeconds >= startTime);
			task.active = taskIsActive;
			return endTime;
		}, 0); */
	}
	
	addNewTask() {
		let duration = constants.DEFAULT_TASK_LENGTH;
		if (duration > state.timeLeftInTheDay) {
			duration = state.timeLeftInTheDay;
		}
		state.tasks.push({
			duration,
			active: false,
			complete: false,
			title: '',
		});
	}
	
	deleteTask(index) {
		state.tasks.splice(index, 1);
	}
	
	completeTask(index) {
		state.tasks[index].complete = true;
	}
	
	toggleTaskStatus(index) {
		let task = state.tasks[index];
		if (task.complete) {
			task.complete = false;
		} else {
			task.complete = true;
		}
	}
	
	startTimer() {
		if (!this._timerRunning) {
			state.dayHasStarted = true;
			this._timerRunning = true;
			this._timer.start();
		}
	}
	
	stopTimer() {
		state.dayHasStarted = false;
		this._timerRunning = false;
		this._timer.stop();
	}
	
	resetTimer() {
		this.stopTimer();
		state.dayProgressInSeconds = 0;
	}
	
}

export default new TaskTimerController();