import Vue from 'vue';

Vue.directive('attach-element', {
	bind: (el, binding) => {
		Vue.nextTick(() => {
			const containerId = binding.value.container;
			const container = document.getElementById(containerId);
			el.observer = new MutationObserver((mutationsList, observer) => {
				const siblingNodes = container.childNodes;
				const lastSiblingNode = siblingNodes[siblingNodes.length - 1];
				const rect = lastSiblingNode.getBoundingClientRect();
				el.style.top = rect.top + 'px';
				el.style.left = rect.left + rect.width + 'px';
				el.style.height = rect.height + 'px';
			});
			// Start observing the target node for configured mutations.
			el.observer.observe(container, {
				attributes: true,
				childList: true,
				subtree: true
			});
		});
	},
	unbind: (el) => {
		el.observer.disconnect();
		el.observer = null;
	},
});