<template>
  <div class="go-toast-wrapper">
    <transition name="slide-up-and-down" @after-leave="onTransitionLeave">
      <div v-show="visible" class="go-toast shadow">
        <div class="go-toast__text">
          <h4 v-if="title" class="go-toast__title">{{ title }}</h4>
          <p v-if="message" class="go-toast__message" v-html="message"></p>
        </div>
        <go-button type="link" @click="hide">CLOSE</go-button>
      </div>
    </transition>
  </div>
</template>

<script>
  
  import GoButton from '../GoButton';
  
  export default {
    name: 'GoToast',
    props: {
      title: String,
      message: String,
      duration: {
        type: Number,
        default: 3000,
      },
    },
    components: {
      GoButton
    },
    methods: {
      onTransitionLeave() {
        this.$destroy();
      },
      hide() {
        if (this.hideTimerId) {
          clearTimeout(this.hideTimerId);
          this.hideTimerId = null;
        }
        this.visible = false;
      },
      show() {
        this.visible = true;
      }
    },
    beforeDestroy() {
      this.$el.parentNode.removeChild(this.$el);
    },
    mounted() {
      document.body.appendChild(this.$el);
      if (this.duration) {
        this.hideTimerId = setTimeout(() => {
          this.hide();
        }, this.duration);
      }
    },
    data() {
      return {
        hideTimerId: null,
        visible: false,
      }
    }
  }
  
</script>

