  }
  
  .slide-up-and-down {
    backface-visibility: hidden;
  }
  
  .slide-up-and-down-enter {
    transform: translateY(32px) scale(0.975, 0.975);
    opacity: 0;
  }
  
  .slide-up-and-down-enter-to {
    transform: translateY(0) scale(1.0, 1.0);
